"use client";
import { useState } from "react";

const mockRiskyPositionsInit = [
	{
		id: 3,
		asset: "ETH",
		size: 1,
		entryPrice: 1800,
		margin: 100,
		leverage: 10,
		isLong: true,
		isOpen: true,
		health: 0.6,
	},
	{
		id: 4,
		asset: "BTC",
		size: 0.5,
		entryPrice: 32000,
		margin: 200,
		leverage: 8,
		isLong: false,
		isOpen: true,
		health: 0.7,
	},
];

export default function LiquidationsPage() {
	const [positions, setPositions] = useState<any[]>(mockRiskyPositionsInit);
	const [message, setMessage] = useState("");

	function handleLiquidate(id: number) {
		setPositions((p) =>
			p.map((pos) =>
				pos.id === id ? { ...pos, isOpen: false, liquidated: true } : pos
			)
		);
		setMessage("Position liquidated");
	}

	return (
		<div className="centered-container">
			<div className="max-w-xl w-full">
				<h1 className="text-3xl font-bold mb-4 shadow-title">Liquidations</h1>
				<p className="text-gray-600 mb-6">
					Monitor and trigger liquidations for risky positions.
				</p>
				<div className="card">
					<h2 className="text-xl font-semibold mb-2">Risky Positions</h2>
					<table className="w-full text-left">
						<thead>
							<tr>
								<th className="py-1">ID</th>
								<th className="py-1">Asset</th>
								<th className="py-1">Size</th>
								<th className="py-1">Leverage</th>
								<th className="py-1">Health</th>
								<th className="py-1">Status</th>
								<th className="py-1">Action</th>
							</tr>
						</thead>
						<tbody>
							{positions.length === 0 && (
								<tr>
									<td
										colSpan={7}
										className="text-gray-400 py-2 text-center"
									>
										No risky positions
									</td>
								</tr>
							)}
							{positions.map((pos) => (
								<tr key={pos.id} className="border-t">
									<td className="py-1">{pos.id}</td>
									<td className="py-1">{pos.asset}</td>
									<td className="py-1">{pos.size}</td>
									<td className="py-1">{pos.leverage}x</td>
									<td className="py-1">
										{(pos.health * 100).toFixed(0)}%
									</td>
									<td className="py-1">
										{pos.isOpen
											? pos.liquidated
												? "Liquidated"
												: "Open"
											: "Closed"}
									</td>
									<td className="py-1">
										{pos.isOpen && !pos.liquidated && (
											<button onClick={() => handleLiquidate(pos.id)}>
												Liquidate
											</button>
										)}
										{pos.liquidated && (
											<span className="text-[#e11d48] font-semibold">
												Liquidated
											</span>
										)}
										{!pos.isOpen && !pos.liquidated && (
											<span className="text-gray-400">Closed</span>
										)}
									</td>
								</tr>
							))}
						</tbody>
					</table>
					{message && (
						<div className="mt-4 text-[#6366f1] font-semibold">
							{message}
						</div>
					)}
				</div>
			</div>
		</div>
	);
}